@import "../mixins.scss";
@import "../variables.scss";

.faq {
  .accordion {
    .itembox {
      padding: 0 10px;

      .title {
        cursor: pointer;
        transition: background-color 0.3s ease;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 20px;
        margin: 10px auto;

        border-radius: $middle-radius;
        background-color: $dark-brown;
        h2,
        span {
          color: $white-brown;
          &:hover {
            color: $orange-icon;
          }
        }

        h2 {
          font-size: $middle-font;
          @media (max-width: 700px) {
            font-size: $standart-font;
          }
        }
        span {
          font-size: 30px;
          font-weight: 900;
        }
      }
      .content {
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.7s ease;
        padding: 0 30px;
        text-align: justify;

        &.show {
          max-height: 500px;
          transition-duration: 2s;
        }
      }
    }
  }
}
