@import "./mixins.scss";
@import "./variables.scss";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  color: $text-brown;
  list-style: none;
  font-family: "Chakra Petch", sans-serif;
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 1px;
}

.app {
  background-color: $all-background;
  min-height: 100vh;
}

.row {
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
}

.row2 {
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
}

.row3 {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
